body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  background-color: #2d0b23; /* Pink background */
  color: black; /* Text color */
  line-height: 1.5; /* Line height */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace; /* Code font family */
}

.container {
  color: aqua; /* Text color for elements with class 'container' */
}

:root {
  --clr-pink: #f403b0; /* Custom pink color variable */
}

.section {
  width: 60vw; /* Set the width to 90% of the viewport width */
  margin: 0 auto; /* Center the section horizontally */
  max-width: var(
    --clr-pink
  ); /* Maximum width based on custom pink color variable */
  padding: 20px; /* Add some padding for spacing */
  background-color: #ffffff; /* Set the background color */
  border-radius: 10px; /* Add rounded corners */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Add a subtle box shadow */
}

.person img {
  width: 75px; /* Image width */
  height: 75px; /* Image height */
  object-fit: cover; /* Image cover */
  border-radius: 50%; /* Rounded border */
  display: flex;
  align-items: center;
  align-content: center;
}
.person {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px;
  margin: 15px;
}

.box {
  background-color: #f403b0;
}
.namee{
  text-align: center;
  padding: 1px;
}